@import url(https://fonts.googleapis.com/css?family=Montserrat:500,600&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
  color: #191919;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box
}
.Apartment{position:absolute;right:30px;top:0;width:328px;box-shadow:0 10px 20px rgba(0,0,0,0.1);border-radius:0 0 6px 6px;transition:all .3s;background:#fff;display:block;border:1px solid #e1e4e8;padding:24px 24px 16px 24px;margin-bottom:32px}@media (max-width: 992px){.Apartment{left:50%;right:auto;bottom:-32px;top:auto;transform:translateX(-50%);box-shadow:none;border-radius:6px 6px 0 0}}.Apartment__description{color:#8a94a6}.Apartment__accent{font-weight:600;font-size:18px;line-height:32px}.Apartment__station>span:last-child,.Apartment__container>span:last-child{color:#8a94a6}.Apartment__station{margin-bottom:8px;padding-bottom:15px;padding-left:5px;padding-right:5px;margin-left:-5px;margin-right:-5px;border-bottom:1px solid rgba(0,0,0,0.1)}.Apartment__station p{margin:0;font-size:14px;line-height:21px;font-weight:500}.Apartment__container>span:first-child{font-size:14px;display:block}.Apartment__container>span:last-child{font-size:12px;line-height:16px}.Apartment__mark{display:inline-block;width:8px;height:8px;border-radius:50%;margin-right:12px}.Apartment__details{display:flex;align-items:baseline;justify-content:space-between}.Apartment__info{display:flex;justify-content:space-between;margin-top:15px}.Apartment sup{position:relative;font-size:10.5px;top:1px}.Apartment__icon-checked{margin-right:15px;vertical-align:-1px}.Apartment__status{padding-top:4px;padding-bottom:4px;padding-left:10px;padding-right:10px;font-weight:600;font-size:12px;border-radius:6px}.Apartment__status_booked{background-color:#ffecc7;color:#f07300}

.CarouselItem{position:relative}.CarouselItem img{display:block;width:100%;height:472px;object-fit:cover}

.AppCarousel{position:relative}.AppCarousel__controls{display:flex;align-items:baseline;box-shadow:0 10px 20px rgba(0,0,0,0.1);border-radius:6px 6px 0 0;background:#fff;padding:12px 24px;width:328px;height:56px;right:30px;position:absolute;top:-56px;border-bottom:1px solid #e1e4e8}@media (max-width: 992px){.AppCarousel__controls{top:auto;bottom:-56px;left:50%;justify-content:space-between;transform:translateX(-50%);border-radius:0 0 6px 6px}}.AppCarousel__counter{margin-right:30px;font-weight:600;line-height:22px}.AppCarousel__button{background:#f1f2f4;border:0;outline:0;height:32px;width:32px;border-radius:50%;cursor:pointer}.AppCarousel__button:first-child{margin-right:15px}.AppCarousel__arrow{position:relative;top:1px;font-size:16px;font-weight:600}button:disabled .AppCarousel__arrow{display:none}.AppCarousel__chevron::before{position:relative;top:-1px;content:'';display:none;height:0.45em;width:0.45em;font-size:16px;border-width:0.12em 0.12em 0 0;border-style:solid;border-width:0.12em 0.12em 0 0}button:disabled .AppCarousel__chevron::before{display:inline-block}.AppCarousel__chevron.left::before{transform:rotate(-135deg)}.AppCarousel__chevron.right::before{left:-1px;transform:rotate(45deg)}.AppCarousel .carousel__back-button,.AppCarousel .carousel__next-button{padding-left:0;padding-right:0;border:none;outline:0}

.ApartmentsDemo{background-color:#f8f9fb}@media (max-width: 992px){.ApartmentsDemo{padding-bottom:100px}}.ApartmentsDemo__intro{padding-top:45px;padding-bottom:60px}@media (max-width: 992px){.ApartmentsDemo__intro{padding-bottom:35px}}.ApartmentsDemo__container{display:flex;justify-content:space-between;width:98%;max-width:1400px;padding-left:15px;padding-right:15px;margin:auto}.ApartmentsDemo__container:nth-child(2){margin-top:10px}@media (max-width: 992px){.ApartmentsDemo__container:nth-child(2){margin-top:25px}}@media (max-width: 992px){.ApartmentsDemo__container{display:block}}.ApartmentsDemo__info{width:58%}@media (max-width: 992px){.ApartmentsDemo__info{width:100%;margin-bottom:20px}}.ApartmentsDemo__details{display:flex;justify-content:flex-end;width:42%}.ApartmentsDemo__details>div{width:30%}@media (max-width: 992px){.ApartmentsDemo__details>div{width:50%}}@media (max-width: 992px){.ApartmentsDemo__details>div:last-child{margin-top:20px}}@media (max-width: 992px){.ApartmentsDemo__details{width:100%;justify-content:space-between;flex-wrap:wrap}}.ApartmentsDemo__accent{display:block;margin-bottom:5px;font-weight:600;font-size:24px;line-height:24px}.ApartmentsDemo__description{display:block;max-width:120px;font-weight:500;font-size:14px;line-height:21px;color:#8a94a6}

.App {
  padding-top: 40px;
  padding-bottom: 40px;
}

.link {
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
}

.link_as-button {
  transition: color 0.3s;
  border-radius: 6px;
  display: inline-block;
  height: 48px;
  line-height: 48px;
  padding-left: 16px;
  padding-right: 16px;
  color: #fff;
  background-color: #f57c00;
}

@media (max-width: 992px) {
  .link_as-button {
    width: 100%;
  }
}

.link_as-button:hover {
  background-color: #e56c00;
}

h2 {
  margin-top: 0;
  font-size: 24px;
  line-height: 33px;
  font-weight: 600;
}

